export default function TeamPage() {
  const socket = io().connect();

  const { secret } = useParams();
  const message = useRef("");
  const [messages, setMessages] = useState([]);
  const [channels, setChannels] = useState([]);
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const [openChannel, setOpenChannel] = useState();

  useEffect(() => {
    console.log(socket);
    socket.on("message", function (data) {
      console.log(data);
    }); 
    loadChannels();
    // var elmnt = document.getElementById("50");
    // elmnt.scrollIntoView();
  }, []);

  function sendMessage(is_vc = false) {
    console.log(message.current.value);
    console.log(socket)
    socket.emit("send", { room: openChannel.secret, message: message.current.value });
    var data = JSON.stringify({ text: message.current.value });

    var config = {
      method: "put",
      url: `/api/secure/messages/${openChannel.secret}`,
      headers: {
        Authorization: `Bearer ${Cookies.get("token")}`,
        "Content-Type": "application/json",
      },
      data: data,
    };

    axios(config)
      .then(function (response) {
        console.log(JSON.stringify(response.data));
      })
      .catch(function (error) {
        console.log(error);
      });
  }
  function loadMessages(channel) {
    var config = {
      method: "get",
      url: `/api/secure/messages/${channel.secret}`,
      headers: {
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    };

    axios(config)
      .then(function (response) {
        console.log(JSON.stringify(response.data));
        setMessages(response.data);
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  function loadChannels() {
    var config = {
      method: "get",
      url: `/api/secure/channels/${secret}`,
      headers: {
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    };

    axios(config)
      .then(function (response) {
        console.log(JSON.stringify(response.data));
        setChannels(response.data);
        clickChannel(response.data[0]);
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  function clickChannel(channel) {
    if(openChannel)
    socket.emit("unsubscribe", openChannel.secret);
    setOpenChannel(channel);
    socket.emit("subscribe", channel.secret);
    loadMessages(channel);
  }
  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="absolute"
        className={clsx(classes.appBar, open && classes.appBarShift)}
      >
        <Toolbar className={classes.toolbar}>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            className={clsx(
              classes.menuButton,
              open && classes.menuButtonHidden
            )}
          >
            <ChevronRightIcon />
          </IconButton>
          <Typography
            component="h1"
            variant="h4"
            color="inherit"
            noWrap
            className={classes.title}
          >
            Relier
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        classes={{
          paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
        }}
        open={open}
      >
        <List>
          <ListItem button>
            <ListItemIcon onClick={handleDrawerClose}>
              <ChevronLeftIcon />
            </ListItemIcon>
            <ListItemText primary="Team name" />
          </ListItem>
        </List>
        {/* <div className={classes.toolbarIcon}>
          <IconButton onClick={handleDrawerClose}>
            <ChevronLeftIcon />
          </IconButton>
              </div>
     */}
        <Divider />
        <List
          style={{
            height: "calc(100vh-179px}",
            overflowY: "auto",
            overflowX: "hidden",
          }}
        >
          <div>
            {channels.map((channel) => (
              <ListItem button>
                <ListItemIcon>
                  <Avatar variant="rounded" className={classes.orange}>
                    {channel.name.substring(0, 1)}
                  </Avatar>
                </ListItemIcon>
                <ListItemText primary={channel.name} />
              </ListItem>
            ))}
          </div>
        </List>
        <List className={classes.bottomToolbar}>
          <Divider />
          <ListItem button style={{ paddingTop: 10 }}>
            <ListItemIcon style={{ paddingLeft: 6 }}>
              <AddIcon color="secondary" />
            </ListItemIcon>
            <ListItemText primary="Add channels" />
          </ListItem>
          <ListItem button>
            <ListItemIcon style={{ paddingLeft: 6 }}>
              <SearchIcon color="secondary" />
            </ListItemIcon>
            <ListItemText primary="Browse channels" />
          </ListItem>
        </List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container
          maxWidth="lg"
          className={classes.container}
          style={{ paddingBottom: 0 }}
        >
          <Grid container spacing={3} style={{ paddingBottom: 0 }}>
            <Grid item xs={12}>
              {messages.map((message) => (
                <Paper
                  id={message.id}
                  elevation={1}
                  style={{ padding: 12, marginBottom: 24 }}
                >
                  <Grid container spacing={3} style={{ overflow: true }}>
                    <Grid item xs={1} style={{ paddingBottom: 0 }}>
                      <Avatar
                        variant="rounded"
                        className={classes.orange}
                        style={{
                          height: 32,
                          width: 32,
                          margin: "auto",
                          paddingBottom: 0,
                        }}
                      >
                        {message.sender.name.substring(0, 1)}
                      </Avatar>
                    </Grid>
                    <Grid
                      item
                      xs={5}
                      style={{ paddingLeft: 0, paddingBottom: 0 }}
                    >
                      <Typography align="left" color="primary">
                        {message.sender.name}
                      </Typography>
                    </Grid>
                    <Grid item xs={6} style={{ paddingBottom: 0 }}>
                      <Typography align="right" color="textSecondary">
                        {message.timestamp}
                      </Typography>
                    </Grid>
                    <Grid
                      item
                      xs={11}
                      style={{
                        marginLeft: "auto",
                        paddingLeft: 0,
                        paddingTop: 0,
                      }}
                    >
                      <Typography align="left">{message.text}</Typography>
                    </Grid>
                  </Grid>
                </Paper>
              ))}
            </Grid>
            <Grid
              item
              xs={12}
              style={{
                position: "sticky",
                bottom: 0,
                paddingBottom: 24,
                paddingTop: 24,
                backgroundColor: "#fafafa",
                width: 900,
              }}
            >
              <TextField
                id="outlined-textarea"
                label="Message"
                placeholder="Message"
                inputRef={message}
                type="text"
                multiline
                variant="outlined"
                fullWidth
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="send"
                        edge="end"
                        onClick={() => {
                          sendMessage();
                        }}
                      >
                        <SendIcon />
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
          </Grid>
        </Container>
      </main>
    </div>
  );
}
